import forumobjct from "@/components/script/forumobjct";
import videoobject from "@/components/script/videoobject";

export default {
  mixins: [forumobjct, videoobject],
  name: "releated_list",
  props: {
    reletad_list: undefined,
  },
  data() {
    return {
      video_class_name: '',
      show_releated_list: undefined,
      show_length: 9,
      show_video_list: true
    }
  },
  created() {
    this.video_class_name = this.video_col_class_name();
    console.log('reletad_list:', this.reletad_list);
    this.update_show();
  },
  methods: {
    update_releated_list: function () {
      this.show_video_list = false;
      this.$nextTick(() => {
        this.show_video_list = true;
      });
    },
    on_show_more: function () {
      this.show_length += 9;
      console.log('[on_show_more]:', this.show_length);
      this.update_show();
      this.update_releated_list();
    },
    update_show: function () {
      if (this.reletad_list) {
        if (this.reletad_list.datas.length > this.show_length) {
          this.show_releated_list = this.reletad_list.datas.slice(0, this.show_length);
        } else {
          this.show_releated_list = this.reletad_list.datas;
        }
      }
      console.log('show_releated_list:', this.show_releated_list);
    }
  },
  computed: {
    is_show_more: function () {
      if (!this.reletad_list) {
        return false;
      }
      return this.show_length < this.reletad_list.datas.length;
    }
  }
}
